import React, { Component } from 'react';
import Carousel from 'nuka-carousel';
import {
  H1,
  H2,
  ExchangeCardShow,
  BasicCardShow,
  ProtectionCardShow,
  SvgCard,
  LeftArrow,
  RightArrow
} from './styles.js';

class CardType extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedOption: 'option1'
    };
  }

  setCard = type => {
    const setPriceCard = this.props.setPriceCard;
    if (type === 0) {
      setPriceCard(500);
    } else if (type === 1) {
      setPriceCard(700);
    } else {
      setPriceCard(950);
    }
  };

  sportPrice = e => {
    const price = this.props.priceCard;
    const setPriceCard = this.props.setPriceCard;
    e.target.checked ? setPriceCard(price + 250) : setPriceCard(price - 250);
  };

  insurancePrice = e => {
    this.setState({ selectedOption: e.target.name });
    const price = this.props.priceCard;
    const setPriceCard = this.props.setPriceCard;
    const number = Number(e.target.value);
    number === 250
      ? setPriceCard(price - 500 + number)
      : setPriceCard(price - 250 + number);
  };

  render() {
    const { priceCard } = this.props;
    const { selectedOption } = this.state;
    return (
      <>
        <H1>Выбор карты «Проф Плюс»</H1>
        <Carousel
          renderCenterLeftControls={({ previousSlide }) => (
            <LeftArrow onClick={previousSlide} />
          )}
          renderCenterRightControls={({ nextSlide }) => (
            <RightArrow onClick={nextSlide} />
          )}
          renderBottomCenterControls={null}
          afterSlide={slideIndex => this.setCard(slideIndex)}
          cellAlign="right"
          transitionMode="scroll3d"
          slideWidth="350px"
          slideIndex={1}
          opacityScale={0.3}
        >
          <ExchangeCardShow>
            {SvgCard.big} <h1>«Обмен»</h1>
            <ul>
              <li>Скидки и бонусы в магазинах Вашего города;</li>
              <li>Оплата проезда в общественном транспорте;</li>
              <li>Cкидки от 20% сотовых операторов;</li>
              <li>Страхование от НС до 200 тыс.руб.;</li>
            </ul>
          </ExchangeCardShow>

          <BasicCardShow>
            {SvgCard.big}
            <h1>«Базовая»</h1>
            <ul>
              <li>Скидки и бонусы в магазинах Вашего города;</li>
              <li>30% скидки у сотовых операторов;</li>
              <li>Оплата проезда в общественном транспорте;</li>
            </ul>
          </BasicCardShow>

          <ProtectionCardShow>
            {SvgCard.big}
            <h1>«Защита»</h1>
            <ul>
              <li>Скидки и бонусы в магазинах Вашего города;</li>
              <li>Оплата проезда в общественном транспорте;</li>
              <li>Cкидки от 20% сотовых операторов;</li>
              <li>Страхование от НС до 200 тыс.руб.;</li>
            </ul>
            <div />
            <div>
              <input
                name="option1"
                type="radio"
                value="250"
                checked={selectedOption === 'option1'}
                onChange={this.insurancePrice}
              />
              <label>100 тыс. рублей</label>
            </div>
            <div>
              <input
                type="radio"
                name="option2"
                value="500"
                checked={selectedOption === 'option2'}
                onChange={this.insurancePrice}
              />
              <label>200 тыс. рублей</label>
            </div>
            <div>
              <input
                type="checkbox"
                id="inlineCheckbox1"
                value={priceCard}
                onChange={this.sportPrice}
              />
              <label>Включить спортивный риск</label>
            </div>
          </ProtectionCardShow>
        </Carousel>
        <H2>{`Стоимость карты: ${priceCard} ₽`}</H2>
      </>
    );
  }
}

export default CardType;
