import styled from 'styled-components';
import React from 'react';
import listicon from '../../img/list-icon.png';
import leftArrow from '../../img/arrow-left.svg';
import rightArrow from '../../img/arrow-right.svg';

export const H1 = styled.h1`
  font-size: 1.625em;
  font-weight: 700;
  color: #48453c;
  font-family: 'Fira Sans', sans-serif;
  text-align: center;
`;

export const H2 = styled.h2`
  font-size: 1.625em;
  font-weight: 700;
  color: #f31c4c;
  font-family: 'Fira Sans', sans-serif;
  text-align: center;
`;

export const ProtectionCardShow = styled.div`
  right: 0;
  top: 0;
  width: 300px;
  & div {
    padding-left: 30px;
  }
  & h1 {
    font-size: 1.625em;
    font-weight: 700;
    color: #f31c4c;
    font-family: 'Fira Sans', sans-serif;
    text-align: center;
    margin: 0 0 15px 0;
  }
  & ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  & li {
    padding: 5px 0 5px 40px;
    position: relative;
    font-size: 0.9em;
    color: #47433d;
    font-family: 'Fira Sans', sans-serif;
    &:before {
      position: absolute;
      left: 16px;
      top: 6px;
      content: '';
      display: inline-block;
      width: 15px;
      height: 15px;
      background: url(${listicon}) no-repeat;
      background-position: center;
      background-size: contain;
    }
  }
`;
export const BasicCardShow = styled(ProtectionCardShow)``;
export const ExchangeCardShow = styled(ProtectionCardShow)``;

export const LeftArrow = styled.div`
  cursor: pointer;
  width: 30px;
  height: 40px;
  background: url(${leftArrow}) no-repeat;
`;

export const RightArrow = styled.div`
  cursor: pointer;
  width: 30px;
  height: 40px;
  background: url(${rightArrow}) no-repeat;
`;
export const SvgCard = {
  big: (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      width="300px"
      height="250px"
    >
      <image
        x="0px"
        y="0px"
        width="300px"
        height="250px"
        xlinkHref="data:img/png;base64,"
      />
    </svg>
  )
};
