import React, { Component } from 'react';
import { Block, Image, TextBlock, H1 } from './styles';

class FinalStep extends Component {
  componentDidMount() {
    this.props.finalStep();
  }
  render() {
    return (
      <>
        <Block>
          <Image />
          <TextBlock>
            <H1>
              Ваша <br />
              заявка принята!
            </H1>
            <span>Мы свяжемся с вами ближайшее время.</span>
          </TextBlock>
        </Block>
      </>
    );
  }
}

export default FinalStep;
