import styled from 'styled-components';

export const Header = styled.div`
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
`;

export const BreadCrumbs = styled.div`
  font-size: 0.75em;
  color: #515151;
  text-transform: uppercase;
  font-family: 'Fira Sans', sans-serif;
  & :first-child {
    color: #515151;
    :after {
      content: '>';
      display: inline-block;
      margin: 0 7px 0 10px;
    }
  }
  & a {
    color: #f31c4c;
    text-decoration: none;
  }
`;
export const Steps = styled.div`
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  width: 350px;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 1000px) {
    display: none;
  }
  & :first-child {
    & :before {
      display: none;
    }
  }
  & :last-child {
    & :after {
      display: none;
    }
  }
`;
export const Step = styled.div`
  display: flex;
  flex-wrap: nowrap;
  & :before {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: -100%;
    height: ${props => (props.active ? '2px' : '1px')};
    width: 35px;
    transform: translateY(-50%);
    background: ${props => (props.active ? '#f31c4c' : '#e2e2e2')};
  }
  & :after {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 100%;
    height: ${props => (props.active ? '2px' : '1px')};
    width: 35px;
    transform: translateY(-50%);
    background: ${props => (props.active ? '#f31c4c' : '#e2e2e2')};
  }

  & a {
    text-decoration: none;
    position: relative;
    z-index: 2;
    height: 35px;
    width: 35px;
    border-radius: 50%;
    text-align: center;
    line-height: 35px;
    color: ${props => (props.active ? '#fff' : '#e2e2e2')};
    font-weight: 700;
    cursor: pointer;
    font-size: 0.9em;
    background: ${props => (props.active ? '#f31c4c' : '#e2e2e2')};
  }
`;

export const Button = styled.button`
  border: none;
  width: 210px;
  height: 31px;
  color: #fff;
  font-size: 1em;
  text-transform: uppercase;
  background-color: ${props => (props.gray ? '#c9c8c8' : '#f31c4c')}
  text-align: center;
  cursor: pointer;
  outline: none;
  font-family: 'Fira Sans', sans-serif;
  margin-left: 10px;
`;
