import React, { Component } from 'react';
import styled from 'styled-components';
import FormHeader from '../components/FormHeader';
import CardReceivingType from '../components/CardReceivingType';
import CardType from '../components/CardType';
import AuthForm from '../components/AuthForm';
import UploadForm from '../components/UploadForm';
import PayForm from '../components/PayForm';
import FinalStep from '../components/FinalStep';

const Wrapper = styled.div`
  width: 940px;
  @media (max-width: 1000px) {
    width: 85%;
  }
  margin: 0 auto;
  padding: 30px;
  border: 1px solid #cbcdcc;
  border-bottom: none;
`;

const FormBody = styled.div`
  width: 100%;
  height: 500px;
  padding-top: 20px;
`;

class CardForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentStep: 1,
      receiveType: 'choosingType',
      priceCard: 700,
      stepPassed: false,
      invoiceUploaded: false,
      finalStep: false
    };
  }

  nextStep = () => {
    this.setState(prevState => ({
      currentStep: prevState.currentStep + 1
    }));
  };

  stepNotPassed = () => {
    this.setState({ stepPassed: false });
  };

  changeStep = step => {
    this.setState({ currentStep: step });
  };

  setReceiveType = type => {
    this.setState({ receiveType: type, stepPassed: true });
    this.nextStep();
  };

  setPriceCard = price => {
    this.setState({ priceCard: price });
  };

  postUserData = data => {
    this.setState({ user: data, stepPassed: true });
  };

  invoiceUploaded = () => {
    this.setState({ invoiceUploaded: true });
  };

  finalStep = () => {
    this.setState({ finalStep: true });
  };

  render() {
    const {
      receiveType,
      currentStep,
      priceCard,
      stepPassed,
      finalStep
    } = this.state;
    const mapSteps = {
      choosingType: {
        1: (
          <CardReceivingType
            setReceiveType={this.setReceiveType}
            receiveType={receiveType}
          />
        )
      },
      individual: {
        2: <CardType priceCard={priceCard} setPriceCard={this.setPriceCard} />,
        3: (
          <AuthForm
            stepNotPassed={this.stepNotPassed}
            postUserData={this.postUserData}
          />
        ),
        4: <PayForm priceCard={priceCard} />,
        5: <FinalStep finalStep={this.finalStep} />
      },
      collective: {
        2: (
          <AuthForm
            stepNotPassed={this.stepNotPassed}
            postUserData={this.postUserData}
          />
        ),
        3: <UploadForm invoiceUploaded={this.invoiceUploaded} />,
        4: <FinalStep finalStep={this.finalStep} />
      }
    };
    return (
      <Wrapper>
        <FormHeader
          currentStep={currentStep}
          nextStep={this.nextStep}
          changeStep={this.changeStep}
          stepPassed={stepPassed}
          finalStep={finalStep}
        />
        <FormBody>
          {mapSteps[receiveType][currentStep] || mapSteps['choosingType'][1]}
        </FormBody>
      </Wrapper>
    );
  }
}

export default CardForm;
