import React, { Component } from 'react';
import validator from 'validator';
import DatePicker, { registerLocale } from 'react-datepicker';
import ru from 'date-fns/locale/ru';
import InputMask from 'react-input-mask';
import 'react-datepicker/dist/react-datepicker.css';
import {
  H1,
  Forms,
  NameInput,
  DatePhone,
  CheckboxContainer,
  HiddenCheckbox,
  StyledCheckbox,
  Icon,
  CheckBoxBlock,
  SpecialMarks,
  EmailInput,
  ErrorText,
  DateInput,
  Input
} from './styles.js';
registerLocale('ru', ru);

const MaskedInput = props => (
  <InputMask
    mask="+7\ 999 999 99 99"
    maskChar=" "
    value={props.value}
    onChange={props.onChange}
    name={props.name}
    placeholder={props.placeholder}
  >
    {inputProps => <Input {...inputProps} />}
  </InputMask>
);

const Checkbox = ({ className, checked, ...props }) => (
  <CheckboxContainer className={className}>
    <HiddenCheckbox checked={checked} {...props} />
    <StyledCheckbox checked={checked}>
      <Icon viewBox="0 0 24 24">
        <polyline points="20 6 9 17 4 12" />
      </Icon>
    </StyledCheckbox>
  </CheckboxContainer>
);

class AuthForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      emailChecked: false,
      specMarkChecked: false,
      error: null,
      user: {
        birthday: null
      }
    };
  }

  componentDidMount() {
    this.props.stepNotPassed();
  }

  sendUserData = () => {
    const { user, emailChecked, specMarkChecked } = this.state;
    const send = this.props.postUserData;
    const dataComplete =
      user.hasOwnProperty('name') &&
      user.hasOwnProperty('phone') &&
      user.hasOwnProperty('birthday') &&
      user.hasOwnProperty('email') &&
      emailChecked &&
      specMarkChecked;
    if (dataComplete) {
      send(user);
    }
  };

  handleCheckboxChange = async e => {
    const type = e.target.name;
    const checked = e.target.checked;
    await this.setState({ [type]: checked });
    this.sendUserData();
  };

  getInputValue = async e => {
    const type = e.target.name;
    const value = e.target.value;
    console.log(type);
    if (this.validateValue(type, value)) {
      await this.setState(prevState => ({
        user: { ...prevState.user, [type]: value }
      }));
      this.sendUserData();
    }
  };

  getDate = date => {
    this.setState(prevState => ({
      user: {
        ...prevState.user,
        birthday: date
      }
    }));
  };

  validateValue = (type, value) => {
    switch (type) {
      case 'name':
        if (value.length < 2) {
          this.setState({ error: 'Короткое Имя' });
          return false;
        } else {
          this.setState({ error: null });
          return true;
        }
      case 'phone':
        if (value.length < 16) {
          this.setState({ error: 'Короткий контактный номер' });
          return false;
        } else {
          this.setState({ error: null });
          return true;
        }
      case 'email':
        if (!validator.isEmail(value)) {
          this.setState({ error: 'Введите корректную почту' });
          return false;
        } else {
          this.setState({ error: null });
          return true;
        }

      default:
        return false;
    }
  };

  render() {
    const { emailChecked, specMarkChecked, error, user } = this.state;
    return (
      <>
        <H1>Авторизация</H1>
        <Forms>
          {error === null ? '' : <ErrorText>{error}</ErrorText>}
          <NameInput
            placeholder="Фамилия Имя Отчество"
            onChange={this.getInputValue}
            name="name"
          />
          <DatePhone>
            <DatePicker
              showYearDropdown
              dateFormat="dd/MM/yyyy"
              popperPlacement="top-end"
              customInput={<DateInput />}
              locale="ru"
              placeholderText="Дата рождения"
              selected={user.birthday}
              onChange={this.getDate}
            />
            <MaskedInput
              placeholder="Контактный телефон"
              name="phone"
              onChange={this.getInputValue}
            />
          </DatePhone>
          <CheckBoxBlock>
            <label>
              <Checkbox
                name="emailChecked"
                checked={emailChecked}
                onChange={this.handleCheckboxChange}
              />
              <span>E-mail</span>
              <EmailInput name="email" onChange={this.getInputValue} />
            </label>
            <SpecialMarks>
              <span>*</span>Получение уведомлений о новостях на сайте
            </SpecialMarks>
          </CheckBoxBlock>
          <CheckBoxBlock>
            <label>
              <Checkbox
                name="specMarkChecked"
                checked={specMarkChecked}
                onChange={this.handleCheckboxChange}
              />
              <span>
                Согласие <br />
                на обработку персональных данных
              </span>
            </label>
            <SpecialMarks>
              <span>*Внимание!</span> Для перехода на следующий этап необходимо
              поставить отметки в окошках
            </SpecialMarks>
          </CheckBoxBlock>
        </Forms>
      </>
    );
  }
}

export default AuthForm;
