import React, { Component } from 'react';

import {
  H1,
  H2,
  TextBlock,
  FormBlock,
  Image,
  DownloadBlock,
  DropContainer,
  UploadBlock
} from './styles.js';

class UploadForm extends Component {
  constructor(props) {
    super(props);
    this.state = { hightlight: false };
    this.fileInputRef = React.createRef();
  }

  openFileDialog = () => {
    if (this.props.disabled) return;
    this.fileInputRef.current.click();
  };

  onFilesAdded = e => {
    const invoice = e.target.files;
    this.setState({ file: invoice });
    this.props.invoiceUploaded();
  };

  onDragOver = e => {
    e.preventDefault();
    if (this.props.disabled) return;
    this.setState({ hightlight: true });
  };

  onDragLeave = () => {
    this.setState({ hightlight: false });
  };

  onDrop = e => {
    e.preventDefault();
    const invoice = e.dataTransfer.files;
    this.setState({ file: invoice, hightlight: false });
    this.props.invoiceUploaded();
  };

  downloadInvoiceBlank = () => {
    fetch('http://localhost:3000/api/files');
  };

  render() {
    return (
      <>
        <H1>Подача заявки</H1>
        <TextBlock>
          <p>
            <span>*</span> для подачи заявки скачайте и заполните форму
          </p>
          <p>
            <span>*</span> загрузите заполненную форму на сайт
          </p>
        </TextBlock>
        <FormBlock>
          <DownloadBlock>
            <Image onClick={this.downloadRandomImage} />
            <H2>
              <span>Скачайте</span>
              <br />
              заявку <br />
              от организации
            </H2>
          </DownloadBlock>
          <UploadBlock>
            <DropContainer
              className={`Dropzone ${this.state.hightlight ? 'Highlight' : ''}`}
              onDragOver={this.onDragOver}
              onDragLeave={this.onDragLeave}
              onDrop={this.onDrop}
              onClick={this.openFileDialog}
              style={{ cursor: this.props.disabled ? 'default' : 'pointer' }}
            >
              <input
                ref={this.fileInputRef}
                className="FileInput"
                type="file"
                multiple
                onChange={this.onFilesAdded}
              />
            </DropContainer>
            <H2>
              <span>Загрузите</span>
              <br />
              заполненную
              <br />
              форму заявки
            </H2>
          </UploadBlock>
        </FormBlock>
      </>
    );
  }
}

export default UploadForm;
