import React from 'react';
import styled from 'styled-components';
import indivSvg from '../../img/indiv.svg';
import indivSvgRed from '../../img/indiv-red.svg';
import collectSvgRed from '../../img/collect-red.svg';
import collectSvg from '../../img/collect.svg';

export const H1 = styled.h1`
  font-size: 1.625em;
  font-weight: 700;
  color: #48453c;
  font-family: 'Fira Sans', sans-serif;
  text-align: center;
`;

export const H2 = styled.h2`
  font-size: 1.4375em;
  font-weight: 700;
  text-transform: uppercase;
  color: #c9c9c9;
  transition: linear 0.5s;
`;

export const ReceivingTypes = styled.div`
  display: flex;
  justify-content: flex-end;
  font-family: 'Fira Sans', sans-serif;
  margin-top: 50px;
  @media (max-width: 1000px) {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  & span {
    font-size: 12px;
    font-weight: 400;
    color: #46433d;
  }
  & div {
    width: 330px;
    @media (max-width: 1000px) {
      width: 200px;
      margin-bottom: 20px;
    }
    transition: linear 0.5s;
    cursor: pointer;
  }
`;

export const IndividualType = styled.div`
  & div {
    width: 330px;
    height: 235px;
    background: url(${indivSvg}) no-repeat;
  }
  &:hover {
    & div {
      background: url(${indivSvgRed}) no-repeat;
    }
    & h2 {
      color: #f31c4c;
    }
  }
`;

export const CollectivelyType = styled.div`
  & div {
    width: 330px;
    height: 235px;
    background: url(${collectSvg}) no-repeat;
  }
  &:hover {
    & div {
      background: url(${collectSvgRed}) no-repeat;
    }
    & h2 {
      color: #f31c4c;
    }
  }
`;
