import styled from 'styled-components';

export const H1 = styled.h1`
  font-size: 1.625em;
  font-weight: 700;
  color: #48453c;
  margin-left: 30%;
  @media (max-width: 1000px) {
    margin-left: 30%;
  }
  font-family: 'Fira Sans', sans-serif;
`;

export const Forms = styled.div`
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  margin-left: 30%;
  @media (max-width: 1000px) {
    margin-left: 0;
  }
  font-family: 'Fira Sans', sans-serif;
`;
export const NameInput = styled.input`
  width: 100%;
  @media (max-width: 1000px) {
    width: 95%;
  }
  height: 35px;
  border: 2px #e2e2e2 solid;
  margin-top: 15px;
  outline: none;
  text-align: center;
  font-style: italic;
  font-size: 16px;
`;
export const DatePhone = styled.div`
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 30px;
  @media (max-width: 1000px) {
    flex-direction: column;
    align-items: center;
  }
`;
export const DateInput = styled.input`
  width: 317px;
  height: 35px;
  border: 2px #e2e2e2 solid;
  margin-top: 15px;
  outline: none;
  text-align: center;
  font-style: italic;
  font-size: 16px;
`;

export const Input = styled.input`
  width: 317px;
  height: 35px;
  border: 2px #e2e2e2 solid;
  margin-top: 15px;
  outline: none;
  text-align: center;
  font-style: italic;
  font-size: 16px;
`;

export const EmailInput = styled.input`
  border: none;
  border-bottom: 2px #c6c6c6 dashed;
  height: 100%;
  outline: none;
  font-size: 16px;
  margin-left: 10px;
`;

export const HiddenCheckbox = styled.input.attrs({ type: 'checkbox' })`
  border: 0;
  clip: rect(0 0 0 0);
  clippath: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: 1px;
`;
export const Icon = styled.svg`
  fill: none;
  stroke: #f31c4c;
  stroke-width: 3px;
`;
export const StyledCheckbox = styled.div`
  display: inline-block;
  width: 20px;
  height: 20px;
  background: white;
  transition: all 150ms;

  ${Icon} {
    visibility: ${props => (props.checked ? 'visible' : 'hidden')};
  }
`;
export const CheckboxContainer = styled.div`
  display: inline-block;
  border: 2px #c8c8c8 solid;
  width: 20px;
  height: 20px;
  margin-right: 15px;
`;

export const CheckBoxBlock = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
  & span {
    display: inline-block;
    color: #c6c6c6;
  }
`;

export const SpecialMarks = styled.span`
  display: inline-block;
  width: 153px;
  font-size: 11px;
  color: #e2e2e2;
  font-weight: 10;
  & span {
    color: #f31c4c;
  }
`;

export const ErrorText = styled.span`
  color: #f31c4c;
  font-size: 12px;
`;
