import styled from 'styled-components';
import uploadSvg from '../../img/upload.svg';
import downloadSvg from '../../img/download.svg';

export const H1 = styled.h1`
  font-size: 1.625em;
  font-weight: 700;
  color: #48453c;
  margin-left: 30%;
  font-family: 'Fira Sans', sans-serif;
`;

export const TextBlock = styled.div`
width:100%
font-family: 'Fira Sans', sans-serif;
& p {
    font-size: 14px;
    color: rgb(187, 186, 186);
    text-align: right;
    margin: 0;
    padding: 5px;
    & span{
        color: #f31c4c;
    }
}
`;
export const FormBlock = styled.div`
  display: flex;
  justify-content: space-around;
  @media (max-width: 1000px) {
    flex-direction: column;
    align-items: center;
  }
  width: 100%;
  margin-top: 40px;
  font-family: 'Fira Sans', sans-serif;
  text-transform: uppercase;
  font-size: 1.625em;
  color: rgb(187, 186, 186);
  & span {
    display: iline-block;
    color: #f31c4c;
  }
`;
export const DownloadBlock = styled.div``;
export const Image = styled.div`
  height: 250px;
  width: 200px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 16px;
  cursor: pointer;
  background: url(${downloadSvg}) no-repeat;
`;
export const H2 = styled.div``;
export const UploadBlock = styled.div`
  @media (max-width: 1000px) {
    margin-top: 30px;
  }
`;
export const DropContainer = styled(Image)`
  background: url(${uploadSvg}) no-repeat;
  & input {
    display: none;
  }
`;
